/* global document */
import ready from '@lskjs/utils/polyfill';
import React from 'react';
import ReactDOM from 'react-dom';

import { App } from './App';

ready();

function includeCSSfile(href) {
  const head_node = document.getElementsByTagName('head')[0];
  const link_tag = document.createElement('link');
  link_tag.setAttribute('rel', 'stylesheet');
  link_tag.setAttribute('type', 'text/css');
  link_tag.setAttribute('href', href);
  head_node.appendChild(link_tag);
}

const main = () => {
  // console.log('main start');
  includeCSSfile('https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css');
  const node = document.createElement('div');
  ReactDOM.render(React.createElement(App), node);
  document.body.appendChild(node);
  // console.log('main finish');
};

main();
